.dashboard-overview {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.dashboard-overview .metrics-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.dashboard-overview .metrics-row .metric-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.dashboard-overview .metrics-row .metric-card .card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}
.dashboard-overview .metrics-row .metric-card .card-header .card-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #6b7280;
  margin: 0;
}
.dashboard-overview .metrics-row .metric-card .card-header .card-icon {
  width: 2.5rem;
  height: 2.5rem;
  background-color: #f3f4f6;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9ca3af;
}
.dashboard-overview .metrics-row .metric-card .card-header .card-icon.warning {
  background-color: #fef3c7;
  color: #f59e0b;
}
.dashboard-overview .metrics-row .metric-card .card-value {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 0.5rem;
}
.dashboard-overview .metrics-row .metric-card .card-value.warning {
  color: #f59e0b;
}
.dashboard-overview .metrics-row .metric-card .card-value.danger {
  color: #ef4444;
}
.dashboard-overview .metrics-row .metric-card .card-trend {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  color: #6b7280;
}
.dashboard-overview .metrics-row .metric-card .card-trend.positive {
  color: #10b981;
}
.dashboard-overview .metrics-row .metric-card .card-trend svg {
  color: #10b981;
}
.dashboard-overview .metrics-row .metric-card .card-message,
.dashboard-overview .metrics-row .metric-card .card-unit,
.dashboard-overview .metrics-row .metric-card .card-description {
  font-size: 0.75rem;
  color: #6b7280;
  margin: 0;
}
.dashboard-overview .classification-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.dashboard-overview .classification-row .classification-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.dashboard-overview .classification-row .classification-card .classification-title {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 1rem 0;
}
.dashboard-overview .classification-row .classification-card .classification-stats {
  margin-bottom: 1rem;
}
.dashboard-overview .classification-row .classification-card .classification-stats .stat-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.dashboard-overview .classification-row .classification-card .classification-stats .stat-item .stat-label {
  font-size: 0.875rem;
  color: #6b7280;
}
.dashboard-overview .classification-row .classification-card .classification-stats .stat-item .stat-value {
  font-size: 0.875rem;
  font-weight: 500;
  color: #111827;
}
.dashboard-overview .classification-row .classification-card .progress-bar {
  width: 100%;
  height: 4px;
  background-color: #f3f4f6;
  border-radius: 2px;
  overflow: hidden;
}
.dashboard-overview .classification-row .classification-card .progress-bar .progress-fill {
  height: 100%;
  background-color: #3b82f6;
  transition: width 0.3s ease;
}
.dashboard-overview .alerts-section {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.dashboard-overview .alerts-section .alerts-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}
.dashboard-overview .alerts-section .alerts-header svg {
  color: #6b7280;
}
.dashboard-overview .alerts-section .alerts-header .alerts-title {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.dashboard-overview .alerts-section .alert-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
}
.dashboard-overview .alerts-section .alert-item .alert-left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.dashboard-overview .alerts-section .alert-item .alert-left .warning-icon {
  color: #ef4444;
}
.dashboard-overview .alerts-section .alert-item .alert-left .alert-content {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.dashboard-overview .alerts-section .alert-item .alert-left .alert-content .alert-part {
  font-weight: 600;
  color: #111827;
}
.dashboard-overview .alerts-section .alert-item .alert-left .alert-content .alert-details {
  font-size: 0.875rem;
  color: #6b7280;
}
.dashboard-overview .alerts-section .alert-item .alert-right .priority-tag {
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
}
.dashboard-overview .alerts-section .alert-item .alert-right .priority-tag.high {
  background-color: #ef4444;
  color: white;
}
.dashboard-overview .fab {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.dashboard-overview .fab:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.dashboard-overview .fab svg {
  color: white;
}
